import Platform from "../../utils/platform";
import { IPlatform } from "./inters";

class WXPlatform implements IPlatform {

    start(onReady: () => void) {
        Laya.MiniAdpter.init(true);
        Platform.execWX('showShareMenu', {});
        onReady && onReady();
    }

    share(options?: object) {
        Platform.execWX('shareAppMessage', options);
    }

    exit() {
        Platform.execWX('exitMiniProgram');
    }

    onLoaded() {
        // do nothing
    }

    setLoadingProgress(percentage: number) {
        // do nothing
    }

}

export const wxPlatform = new WXPlatform;