let _env = '${env}';

/**
 * getEnv
 */
function getEnv() {
    if (_env.indexOf('${') === 0) {
        return 'development';
    }
    return _env;
}

/**
 * setEnv
 * @param env 
 */
function setEnv(env) {
    _env = env;
}

/**
 * isDev
 */
function isDev() {
    return getEnv() !== 'production';
}

/**
 * isProd
 */
function isProd() {
    return getEnv() === 'production';
}

export default {
    getEnv,
    setEnv,
    isDev,
    isProd
}