import './polyfill'
import { initNavigator } from "./services/navigator/navigator_init";
import { wxPlatform } from "./services/platform/platform_wx";
import { fbPlatform } from "./services/platform/platform_fb";

import navigator from "./services/navigator/navigator_stack";
import screen, { initScreen } from "./services/display/screen";
import bg from "./services/display/background";
import env from "./utils/env";
import platform from "./utils/platform";
import popup from "./services/display/popupmanager";
import toast from "./services/display/toastmanager";

import Activity from "./services/display/activity";
import PopupView from "./services/display/popupview";
import ToastView from "./services/display/toastview";

import { runtime } from "./utils/runtime";
import { setDebug, printDebug } from './utils/logger';
import { getVersion } from './utils/version';

function init(width: number, height: number, ...options) {
    printDebug(`init version:${getVersion()}`);
    initScreen(false, width, height, ...options);
}

function init3D(width: number, height: number, ...options) {
    printDebug(`init version:${getVersion()}`);
    initScreen(true, width, height, ...options);
}

function start(options) {
    let newOptions = {
        mainPage: options.mainPage,
        commonRes: options.commonRes,
        fileVersion: options.fileVersion,
        onLoadProgress: (progress) => {
            if (platform.isFacebookApp()) {
                fbPlatform.setLoadingProgress(progress);
            } else if (platform.isWechatApp()) {
                wxPlatform.setLoadingProgress(progress);
            }
            options.onLoadProgress && options.onLoadProgress(progress);
        },
        onLoaded: () => {
            if (platform.isFacebookApp()) {
                fbPlatform.onLoaded();
            } else if (platform.isWechatApp()) {
                wxPlatform.onLoaded();
            }
            options.onLoaded && options.onLoaded();
        }
    }
    let onStart = () => {
        initNavigator(newOptions);
    }
    if (platform.isFacebookApp()) {
        fbPlatform.start(onStart);
    } else if (platform.isWechatApp()) {
        wxPlatform.start(onStart);
    } else {
        onStart && onStart();
    }
}

function share(options: object) {
    if (platform.isFacebookApp()) {
        fbPlatform.share(options);
    } else if (platform.isWechatApp()) {
        wxPlatform.share(options);
    }
}

function exit() {
    if (platform.isFacebookApp()) {
        fbPlatform.exit();
    } else if (platform.isWechatApp()) {
        wxPlatform.exit();
    }
}

const Tape = {
    init,
    init3D,
    start,
    share,
    exit,
    setDebug,
    getVersion,

    bg,
    screen,
    platform,
    env,

    navigator,
    popup,
    toast,

    Activity,
    PopupView,
    ToastView,
}

if (typeof window !== "undefined") {
    (window as any).Tape = Tape;
    (window as any).runtime = runtime;
}

export = {
    Tape,
    runtime
};
