import NavigatorStack from "../navigator/navigator_stack";

export default class Activity extends Laya.Component {

    static open(params, action) {
        NavigatorStack.navigate(this, params, action);
    }

    static finish() {
        NavigatorStack.finish(this);
    }

    static res = [];
    
    page = null;
    params = {};
    duration = 0;
    easeIn = null;
    easeOut = null;
    fromProps = null;
    toProps = null;

    onFocus = null;
    onCreate = null;
    onResume = null;
    onPause = null;
    onDestroy = null;
    onNextProgress = null;

    constructor(options) {
        super();
        this.width = Laya.stage.width;
        this.height = Laya.stage.height;
        this.params = Object.assign({}, options.params || {});
        this.page = options.page;
    }

    set ui(view) {
        view.name = '_contentView';
        this.removeChildren();
        this.addChild(view);
    }

    get ui() {
        return this.getChildByName('_contentView');
    }

    _focus(focus) {
        this.onFocus && this.onFocus(focus);
    }
    _create() {
        this.onCreate && this.onCreate();
    }
    _resume() {
        this.onResume && this.onResume();
    }
    _pause() {
        this.onPause && this.onPause();
    }
    _destroy() {
        this.onDestroy && this.onDestroy();
    }
    _nextProgress(progress) {
        this.onNextProgress && this.onNextProgress(progress);
    }

    //////////////////////////
    /// navigator function
    //////////////////////////

    redirectTo(page, params = {}) {
        this.navigate(page, params, () => {
            this.back();
        });
    }

    navigate(page, params = {}, action = null) {
        NavigatorStack.navigate(page, params, action);
    }

    back() {
        NavigatorStack.finish(this.page, this);
    }

    finish(page, instance = null) {
        NavigatorStack.finish(page, instance);
    }

    pop(number = 1) {
        NavigatorStack.pop(number);
    }

    popToTop() {
        NavigatorStack.popToTop();
    }

}